}

.detail-header {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 8px;
}

.detail-emoji {
  font-size: 16px;
}

.detail-title {
  font-size: 11px;
  font-weight: 600;
  color: #475569;
  flex: 1;
}

.detail-score {
  font-size: 11px;
  font-weight: 700;
  color: #3b82f6;
  background: #dbeafe;
  padding: 2px 6px;
  border-radius: 6px;
}

.detail-content {
  font-size: 10px;
