  border-radius: 16px;
  padding: 16px;
  margin-top: 16px;
  color: white;
  position: relative;
  overflow: hidden;
  display: block;
  visibility: visible;
  opacity: 1;
  width: 100%;
  max-width: 500px;
  height: auto;
  min-height: 200px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

.tarot-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="tarot-stars" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1" fill="white" opacity="0.1"/><circle cx="80" cy="30" r="1" fill="white" opacity="0.1"/><circle cx="40" cy="70" r="1" fill="white" opacity="0.1"/><circle cx="70" cy="80" r="1" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23tarot-stars)"/></svg>');
  pointer-events: none;
  animation: background-shimmer 4s ease-in-out infinite;
}

.tarot-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 30% 20%, rgba(251, 191, 36, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 70% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%);
  pointer-events: none;
  animation: magic-aura 6s ease-in-out infinite;
}

@keyframes background-shimmer {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.6;
  }
}

@keyframes magic-aura {
  0%, 100% {
    opacity: 0.1;
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.1);
  }
}

.tarot-header {
  text-align: center;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;
  /* 모바일에서 가시성 향상 */
  display: block;
  visibility: visible;
  opacity: 1;
}

.tarot-title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
  color: #fbbf24;
  /* 모바일에서 가시성 향상 */
  display: block;
