    display: flex !important;
    visibility: visible !important;
    opacity: 1 !important;
    margin: 20px auto 0 !important;
    padding: 8px 16px !important;
    font-size: 12px !important;
    background: #6b7280 !important;
    color: white !important;
    border: none !important;
    border-radius: 6px !important;
    cursor: pointer !important;
  }
}

/* 모바일 반응형 - 로딩 및 폴백 */
@media (max-width: 768px) {
  .tarot-card-loading,
  .tarot-card-fallback {
    width: 160px;
    height: 240px;
  }
  
  .fallback-symbol {
    font-size: 36px;
    margin-bottom: 8px;
  }
  
  .fallback-text {
    font-size: 14px;
  }
  
  .loading-text {
    font-size: 12px;
  }
}

/* 좋아요 버튼 스타일 */
.like-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.like-button:hover:not(:disabled) {
  background-color: #fef2f2;
  transform: scale(1.1);
}

.like-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* 포인트 메시지 스타일 */
.point-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  padding: 12px 20px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  box-shadow: 0 8px 32px rgba(16, 185, 129, 0.4);
  z-index: 1000;
  animation: pointMessageSlide 3s ease-in-out;
  text-align: center;
  min-width: 200px;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

@keyframes pointMessageSlide {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.8);
  }
  15%, 85% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.8);
  }
}
